/* Default styles for the main container */
body::-webkit-scrollbar{
  width: 0.25rem;
}
body::-webkit-scrollbar-track{
  background:#1e1e24;
}
body::-webkit-scrollbar-thumb{
  background: #a87f0c;
}
.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f5f5f5;
  padding-top: 60px;
  font-size: 16px;
}


.container h1{
    white-space: normal;
    word-break: keep-all;
}


/* Styles for smaller screens (e.g., tablets and mobile devices) */
@media (max-width: 768px) {
  .container {
    padding: 10px; /* Reduce padding for smaller screens */
    font-size: 12px;
  }
}

/* Styles for even smaller screens (e.g., smartphones) */
@media (max-width: 480px) {
  .container {
    padding: 5px; /* Further reduce padding for very small screens */
    font-size: 10px;
  }
}